import { Poppins } from 'next/font/google'
import { 
  FaBoxOpen, 
  FaUserTie, 
  FaChartPie, 
  FaClipboardCheck,
  FaMapMarkedAlt,
  FaTruckLoading,
  FaBell,
  FaUser,
  FaSignOutAlt,
  FaHome,
  FaBoxes,
  FaTruck,
  FaChartLine,
  FaWarehouse,
  FaUsers,
  FaChevronRight
} from 'react-icons/fa'
import Link from 'next/link'

// Configuración de la fuente Poppins
const poppins = Poppins({
  weight: ['400', '500', '600', '700'],
  subsets: ['latin'],
  variable: '--font-poppins',
})

export default function LogisticsDashboard() {
  // Datos combinados
  const dashboardData = {
    // Métricas de tu versión anterior
    pendingOrders: 12,
    lowStockItems: 7,
    recentClients: 3,
    warehouseCapacity: '85%',
    
    // Métricas de las imágenes iniciales
    monthlyShipments: '125,000 kg',
    processedOrders: 89,
    activeClients: 298,
    deliveryRate: '87.3%',
    growth: '+12.8%',
    pendingPayments: 15,
    paymentAmount: 'S/.45,000.00'
  };

  return (
    <div className={`dashboard-container ${poppins.variable}`}>
      {/* Sidebar */}
      <div className="sidebar">
        <div className="sidebar-header">
          <h1><FaHome /> Página Principal</h1>
        </div>
        <nav>
          <ul>
            <li><Link href="/productos"><FaBoxes /> Gestión de Productos</Link></li>
            <li><Link href="/servicios"><FaTruck /> Gestión de Servicios</Link></li>
            <li><Link href="/ventas"><FaChartLine /> Gestión de Ventas</Link></li>
            <li><Link href="/almacen"><FaWarehouse /> Gestión de Almacén</Link></li>
            <li><Link href="/clientes"><FaUsers /> Clientes</Link></li>
            <li><Link href="/proveedores"><FaUserTie /> Proveedores</Link></li>
          </ul>
        </nav>
      </div>

      {/* Main Content */}
      <main className="main-content">
        <header className="dashboard-header">
          <div className="header-content">
            <div>
              <h1>Panel de Control Logístico</h1>
              <p>Resumen operativo</p>
            </div>
            <div className="header-actions">
              <button className="notification-btn">
                <FaBell />
                <span className="badge">3</span>
              </button>
              <div className="user-profile">
                <FaUser /> Admin
                <button className="logout-btn">
                  <FaSignOutAlt />
                </button>
              </div>
            </div>
          </div>
        </header>

        {/* Sección combinada de Métricas */}
        <div className="metrics-grid">
          {/* Métricas de tu versión */}
          <div className="metric-card">
            <div className="metric-icon"><FaTruckLoading /></div>
            <div className="metric-content">
              <h3>Órdenes Pendientes</h3>
              <p className="metric-value">{dashboardData.pendingOrders}</p>
              <p className="metric-label">Por despachar hoy</p>
            </div>
          </div>
          
          <div className="metric-card alert">
            <div className="metric-icon"><FaBoxOpen /></div>
            <div className="metric-content">
              <h3>Stock Bajo</h3>
              <p className="metric-value">{dashboardData.lowStockItems}</p>
              <p className="metric-label">Productos por reabastecer</p>
            </div>
          </div>
          
          <div className="metric-card">
            <div className="metric-icon"><FaUserTie /></div>
            <div className="metric-content">
              <h3>Nuevos Clientes</h3>
              <p className="metric-value">{dashboardData.recentClients}</p>
              <p className="metric-label">Esta semana</p>
            </div>
          </div>
          
          <div className="metric-card">
            <div className="metric-icon"><FaMapMarkedAlt /></div>
            <div className="metric-content">
              <h3>Capacidad Almacén</h3>
              <p className="metric-value">{dashboardData.warehouseCapacity}</p>
              <p className="metric-label">Espacio ocupado</p>
            </div>
          </div>

          {/* Métricas adicionales de las imágenes */}
          <div className="metric-card">
            <h3>Envíos del Mes</h3>
            <p className="metric-value">{dashboardData.monthlyShipments}</p>
            <p className="metric-change positive">+27.6% vs mes anterior</p>
          </div>

          <div className="metric-card">
            <h3>Pedidos Procesados</h3>
            <p className="metric-value">{dashboardData.processedOrders}</p>
            <p className="metric-change negative">-12.5% vs mes anterior</p>
            <p className="metric-detail">30 retrasados</p>
          </div>
        </div>

        {/* Sección de Acciones Principales */}
        <div className="actions-section">
          <h2>Áreas Operativas</h2>
          
          <div className="action-grid">
            <Link href="/control-inventario" className="action-card">
              <div className="action-icon"><FaBoxOpen /></div>
              <div className="action-content">
                <h3>Control de Inventario</h3>
                <p>Gestión completa de productos y existencias</p>
                <div className="action-link">
                  Ver detalles <FaChevronRight />
                </div>
              </div>
            </Link>
            
            <Link href="/gestion-comercial" className="action-card">
              <div className="action-icon"><FaUserTie /></div>
              <div className="action-content">
                <h3>Gestión Comercial</h3>
                <p>Administración de clientes y relaciones</p>
                <div className="action-link">
                  Ver detalles <FaChevronRight />
                </div>
              </div>
            </Link>
            
            <Link href="/reporte-ventas" className="action-card">
              <div className="action-icon"><FaChartPie /></div>
              <div className="action-content">
                <h3>Análisis de Ventas</h3>
                <p>Reportes y tendencias comerciales</p>
                <div className="action-link">
                  Ver detalles <FaChevronRight />
                </div>
              </div>
            </Link>
            
            <Link href="/pedidos" className="action-card">
              <div className="action-icon"><FaTruckLoading /></div>
              <div className="action-content">
                <h3>Operaciones Logísticas</h3>
                <p>Seguimiento de pedidos y despachos</p>
                <div className="action-link">
                  Ver detalles <FaChevronRight />
                </div>
              </div>
            </Link>
          </div>
        </div>

        {/* Sección de Alertas (de las imágenes) */}
        <section className="alerts-section">
          <h2>Alertas del Sistema</h2>
          <div className="alert-card warning">
            <h3>Envíos Retrasados</h3>
            <p>Tienes 30 envíos retrasados por un total de $7,600</p>
            <p>Vencen el 19/06/2025</p>
          </div>
          <div className="alert-card danger">
            <h3>Pagos Pendientes</h3>
            <p>Facturas pendientes de pago por $7,285,000</p>
            <p>Vencen el 19/06/2025</p>
          </div>
          <button className="btn-primary">Ver detalles</button>
        </section>
      </main>
    </div>
  );
}